html,body{
  margin:0;padding:0;
}
ul,li,ol{list-style:none;margin:0;padding:0;}
h1,h2,h3,h4,h5,h6,figure,p{margin:0;}
img{vertical-align:top;}
a{text-decoration:none; color:#fff;cursor:pointer;}

h2{font-size:32px;}
.abi ul{padding:20px 0;}
.abi ul li{font-size:20px; color:#75675d;padding:3px 0;}
.abi ul li:last-child{padding:0px 0;}
ul li small{font-weight:bold;}
*{
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 17px;
  color: #401e20;
  line-height: 1.4;
  letter-spacing: -0.5px;
  word-break: keep-all;
  box-sizing: border-box;
  outline: none;
  margin:0; padding:0;
}
html{scroll-behavior: smooth;}
h2,.engLag,strong{font-family: 'Montserrat', sans-serif;}

header{
  position:fixed;
  top:0;left:0;
  width:20%;
  height:100%;
  text-align:center;
  border-right:1px solid #554a42;
}

.Head-section1{
  padding:30px 0;
  background-color:#fff;
  width:100%;
  height:15%;
}
.Head-section1 p{
  width:100%;
  font-size: 38px;
  font-weight: bold;
}
.Head-section1 p small{
  display:block;
  font-size: 16px;
}
.Head-section2{
  width:100%;
  height:15%;
}
.Head-section2 ul{text-align:right;}
.Head-section2 ul li{
  color:#fff;
  padding:20px;
  font-size: 18px;
  background-color:#75675d;
  border-bottom:1px solid #554a42;
  font-weight: 500;
  cursor: pointer;
}
.Head-section2 ul li:hover{
  background-color:#e8c1a0;
}
.Head-bottom{
  width:100%;
  height:70%;
  background-color: #554a42;

}
.Head-section3{
  text-align:right;
  padding-right:20px;
}
.Head-section3 h2{
  padding:10px 0;
  font-size: 19px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.68;
  letter-spacing: normal;
  text-align: right;
  color: #e8c1a0;
}
.Head-section3 ul li{
  cursor: pointer;
  padding:1px 0;
  font-size: 15px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.85;
  letter-spacing: normal;
  text-align: right;
  color: #75675d;
}
.Head-section3 ul li:hover{
  color:#fff;
}


.Head-section4{  
  padding:30px 0;
}
.Head-section4 ul{
  display:flex;
  align-items: center;
  justify-content: right;
  margin-right:15px;
  padding-bottom:60px;
}
.Head-section4 ul li{
  margin-right:10px;
}

.Photo-contents{
  width:80%;
  margin-left:20%;
  cursor: pointer;
  position:relative;
}
.Photo-contents ul{
  width:100%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul li{
  width:25%;
  position:relative;
}
.Photo-contents ul li img{width:100%;}
.Photo-contents ul li .Photo-text{
  position:absolute;
  top:0; left:0;
  width:100%;
  height:100%;
  background-color:#cfa787;
  opacity: 0;
  padding:80px 0;
  text-align:center;
  transition:.3s;

}
.Photo-contents ul li .Photo-text:hover{
  opacity: 0.9;
  transition:.3s;
}
.Photo-contents ul li .Photo-text p{
  font-size: 25px;
  font-weight: 300;
  color: #fff;
  margin-bottom:20px;
}
.Photo-contents ul li .Photo-text p strong{font-size:20px;}
.Photo-contents ul li .Photo-text span{
  display:block;
  font-size: 14px;
  padding:10px 0 5px 0 ;
  font-weight: 300;
  color: #fff;
}
.Photo-contents ul li .Photo-text span::after{
  content:'';
  display:block;

  width: 57px;
  height: 1px;
  background-color: #fff;
  margin: 20px auto;
}

.Photo-contents ul li .Photo-text img{
  width:59px;
  height:59px;
}

.Quick-menu{
  position:fixed;
  top:0; right:0;
  width:70px;
  height:70px;
  cursor: pointer;
}
.Quick-menu img{width:100%;}
.Quick-contents{
  position:fixed;
  top:70px; right:0;
  width: 350px;
  padding:40px 20px;
  background-color:rgba(0,0,0,0.8);
  display:none;
}
.Quick-contents.on{
  display:block;
}
.Quick-contents ul li{
  display:flex;
  justify-content: flex-start;
  padding:10px 0;
  color:#fff;
  cursor: pointer;
}
.Quick-contents ul li img{width:10%;}
.Quick-contents ul li p{width:80%;padding-left:10px; font-weight:300; color:#fff;}

.mbti-all{
  width:80%;
  margin-left:20%;
  padding-top:100px;
}

.mbti-start{
  width:85%;
  margin:0 auto;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.mbti-enter{
  width:57%;

}
.mbti-content{
  width:43%;
  margin:0 auto;
}



.test-all{
  width:80%;
  border:5px solid #75675d;
  margin:10px 0;
  padding:30px 0;
}
.test{
  width:90%;
  margin:0 auto;
  display:flex;
  justify-content: space-between;
  align-items:center;
  
}
.test label{
  width:30%;  padding:0 5px;
  font-size:20px;
  font-weight: bold;
  white-space: pre;
  margin:0 auto;
}
.test label > small{
  opacity: 0.6;
}
.text-checkbox{
  display:flex;
  
  width:20%;
  margin:0 auto;
  padding:25px 0;
}

.text-checkbox p{
  margin:0 auto;
  padding:0 3px;
  font-size:20px;
}
.text-checkbox input{margin:0 5px; }

.input-box{
  width:40%;
  margin:0 auto;
  position:relative;
  margin-bottom:29px;
}
.input-box p{
  position:absolute;
  top:0; left:0;
  width:90%;
  font-size:16px;
  margin:0 auto;
  font-weight:bold;

}
.input-box p input{
  width:80%;
  padding:5px 10px;
  margin-right:10px;
  font-size:16px;
  border:2px solid #555;
}

.input-box p.on{
  display:none;
}

.clear{
  padding:10px; background-color:#fff;
  border:3px solid #75675d;
  border-radius: 10px;
  font-weight:bold;
  margin-right:10px;
  margin-bottom:150px;
}
.clear:hover{
  background-color:#75675d;
  color:#fff; cursor: pointer;
}
.mbti-bottom{
  width:85%;
  margin:0 auto;
  border-top:1px solid #75675d;
  padding-top:100px;
  display:none;
}
.mbti-bottom.on{display:block;}

.mbti-bottom h2{
  padding-bottom:60px;
}

.mbti-bottom P{
  text-align: center;
  font-size:40px; color:#75675d;
  padding-bottom:60px;
}
.mbti-bottom P strong{
  font-size:44px;
  font-weight:bold;
}

.result{display:none;}
.mbti-detail{display:none;}
.mbti-detail.on{display:block;}

.mbti-detail{
  width:80%;
  margin:0 auto;
  border:5px solid #75675d;
  padding:60px;
}
.mbti-detail p{
  font-size:25px; 
  padding:0; width:90%; 
  margin:0 auto;text-align:left;
  line-height: 2;
}
.again{
  margin-left:10%;
  margin-bottom:150px;
  margin-top:10px;
}

.popup{
  position:fixed;
  top:0; left:0;
  background-color: rgba(0,0,0,0.7);
  width:100%;
  height:100%;
  display:none;
}
.popup.on{
  display:block;
}

.mbti-popup{
  position:absolute;
  width:60%; height:70%;
  top:50%; left:30%;
  transform:translateY(-50%);
  background-color:#f6f7f9;
  border:5px solid #75675d;
  opacity: 0.6;
  padding:50px;
  vertical-align: middle;
  overflow:auto;
  display:none;
}
.mbti-popup.on{
  display:block;
}
.mbti-popup h2{padding-bottom:30px;}
.mbti-popup p{
  width:80%;
  height:70%;
  font-size: 22px;
  line-height:2;
  padding:0; margin:0 auto;
  vertical-align: middle;
}

.close{
  margin:0;
  position:absolute;
  bottom:5%; left:90%;

  padding:10px; background-color:#fff;
  border:4px solid #75675d;
  border-radius: 10px;
  font-weight:bold;
}

.close:hover{
  background-color:#75675d;
  color:#fff; cursor: pointer;
}